@import "variables.scss";

@mixin btn-dark {
  padding: 10px 15px;
  border: 0px solid #ccc;
  border-radius: 5px;
  background-color: $primaryColor;
  color: $light;
  font-size: 0.75rem;
  font-weight: bold;
  width: 100%;
  height: 40px;
  cursor: pointer;
}

@mixin btn-dark-hover {
  @include btn-dark;
  background-color: $primaryColorHover;
  box-shadow: $shadow;
}

@mixin btn-dark-active {
  @include btn-dark;
  background-color: $primaryColorActive;
  box-shadow: $shadowActive;
}

@mixin btn-light {
  padding: 10px 15px;
  border: 1px solid $primaryColor;
  border-radius: 5px;
  background-color: $background;
  color: $primaryColor;
  font-size: 0.75rem;
  font-weight: bold;
  width: 100%;
  height: 40px;
  cursor: pointer;
}

@mixin btn-light-hover {
  @include btn-light;
  background-color: $light;
  box-shadow: $shadow;
}

@mixin btn-light-active {
  @include btn-light;
  background-color: $background;
  box-shadow: $shadowActive;
}

@mixin primary-font {
  color: $primaryFont;
  font-size: 0.875rem;
}

@mixin input {
  padding: 5px;
  border: 0px solid #ccc;
  border-radius: 5px;
  box-sizing: border-box;
  height: 40px;
  font-size: 0.875rem;
  color: $primaryFont;
}

@mixin btn-selected {
  padding: 10px 10px;
  border: 0px solid #ccc;
  border-radius: 5px;
  background-color: $light;
  color: $primaryFont;
  height: 35px;
}
